<<template>
<div>
    
<md-card style="height:520px;width:450px;">
        <md-card-header >
            <md-toolbar :md-elevation="1" style="background-color:#EAECEE; box-shadow: 0 0 10px rgba(0,0,0,0.5);">
                <span class="md-title" >Отдаёте:</span>
            </md-toolbar>
        </md-card-header>

    <md-card-content>
        <md-content class="md-elevation-1">
            <md-field  style="margin:-16px 0 0 0;">
                <p style="margin:7px 0px 0px 12px;font-size:22px;"><b>BYN</b></p>
        
        <md-input v-model="newTransaction.byn" type="number" style="font-size:25px;margin:0 10px 3px 10px"></md-input>
            </md-field>
        </md-content>
    </md-card-content>

    <md-card-header>
        <md-toolbar :md-elevation="1" style="background-color:#EAECEE;box-shadow: 0 0 10px rgba(0,0,0,0.5);">
            <span class="md-title" >Получаете:</span>
            
        </md-toolbar>
    </md-card-header>

    <md-card-content>
        <md-content class="md-elevation-1">
            <md-field  style="margin:-16px 0 0 0;">

                <p style="margin:7px 0px 0px 12px;font-size:22px;padding-bottom:28px;height:20px;"><b>COIN</b></p>
                    
                    <md-input v-model="newTransaction.coin" type="number" disabled style="font-size:25px;margin:6px 10px -10px 10px;height:20px;" >
                        
                    </md-input>
            </md-field>
        </md-content>
    </md-card-content>

     <md-card-header>
        <md-toolbar :md-elevation="1" style="background-color:#EAECEE;box-shadow: 0 0 10px rgba(0,0,0,0.5);">
            <span class="md-title">Кошелёк:</span>
            
        </md-toolbar>
    </md-card-header>

    <md-card-content>
        <md-content class="md-elevation-1">
            <md-field  style="margin:-16px 0 0 0;">

                <p style="margin:7px 0px 0px 12px;font-size:22px;padding-bottom:28px;height:20px;"></p>
                    
                    <md-input v-model="newTransaction.adress"  style="font-size:25px;margin:-1px 10px 10px 3px;height:28px;" >
                        
                    </md-input>
            </md-field>
        </md-content>
    </md-card-content>
<!-- Dialog -->
<md-dialog-alert 
      :md-active.sync="first"
      md-content ="<h3><b>Как обменять?</b></h3><b>1.</b>Введите 
      <br>- в поле 'Отдаёте' сумму,на которую хотите приобрести валюту,<br>- в поле 'Кошелёк' адрес кошелька для выбранной криптовалюты.
      <br>Выберите нужную валюту для покупки в правом столбце.
<br><b>2</b>.Нажмите кнопку «Обменять» и следуйте появившейся инструкции."
    
      md-confirm-text="Закрыть" style="background-color:white;margin: 0px 0px 10px 0px;width:500px;"/>
<!-- Dialog -->

<md-dialog-alert 
      :md-active.sync="second"
      md-content ="<h3><b>Обменять</b></h3>
     - Для окончания процедуры получения криптовалюты требуется произвести перевод имеющихся для обмена BYN на расчетный счет BY000000000000.<br>
     "
      md-confirm-text="Закрыть" style="background-color:white;margin: 0px 0px 10px 0px;width:500px;">
</md-dialog-alert>
<!--  -->




    <md-button class="md-primary md-raised" 
    style=" color:white;background-color:#276CC6;width:140px;height:40px;"
    @click=" second=true;" v-on:click="addTransaction"  >

    Обменять

    </md-button>




    <md-button class="md-accent md-raised" @click="first = true" 
      style="width:140px;height:40px;">
            <md-icon>help_outline
                </md-icon>
    Как обменять
    </md-button>
</md-card>
   <!-- курсы валют -->
    <md-card style="height:520px;width:450px;">
        <md-card-header>
            <md-toolbar :md-elevation="1" style="height:20px;background-color:#EAECEE; box-shadow: 0 0 10px rgba(0,0,0,0.5); width:100%;">
                <span class="md-title"  >Курсы валют:</span>
            </md-toolbar>
        </md-card-header>

        <div   style="margin:-5px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in btc">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange_btc(byn(value))">
            BTC ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>
        <div   style="margin:0px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in bch">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange(byn(value))">
            BCH ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>

        <div   style="margin:0px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in eth">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange(byn(value))">
            ETH ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>

        <div   style="margin:-0px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in ltc">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange(byn(value))">
            LTC ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>
        <div   style="margin:-0px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in xrp">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange(byn(value))">
            XRP ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>
        <div   style="margin:-0px 0px 0px 0px;width:100%;height:50px;"  v-for="(value, key) in xem">
            <md-button class="md-card" style="height:50px;font: normal;font-size:23px;"  @click="exchange(byn(value))">
            XEM ⇒ {{  ((value+5*value/100)*2).toFixed(3) }}
            </md-button> 
        </div>
        
    </md-card>

<div style="height:30px">
</div>

<!-- -->

<div class="container">
   <div class="page-header">
     <h3>История транзакций</h3>
   </div>
   <div class="panel panel-default">
     <md-card  style="width:100%;margin-top:10px;height:auto;padding:0px 50px 10px 50px">
     <div class="panel-body" >
       <table class="table table-striped">
         <thead >
           <tr>
             <th style="text-align:center">Количество BYN</th>
             <th style="text-align:center">Количество Coin</th>
             <th style="text-align:center">Адрес кошелька</th>
             
           </tr>
         </thead>
         <tbody>
           <tr v-for="transaction in transactions">
             <td>{{transaction.byn}}</td>
             <td>{{transaction.coin}}</td>
             <td>{{transaction.adress}}</td>
           </tr>
         </tbody>
       </table>
     </div>
     </md-card>
     
<div style="height:25px;">
</div>

   </div>
 </div>
<!--  -->
</div>
</template>

<script>
/* eslint-disable */
import Firebase from 'firebase'
let config = {
   apiKey: "AIzaSyB-0IMbxvw9MAiY6xh7RNwXlUgt1qgw3Mk",
    authDomain: "vueauth-df886.firebaseapp.com",
    databaseURL: "https://vueauth-df886.firebaseio.com",
    projectId: "vueauth-df886",
    storageBucket: "vueauth-df886.appspot.com",
    messagingSenderId: "266969097618"
 };
 
let app = Firebase.initializeApp(config)
let db = app.database()
let transactionsRef = db.ref('transactions')

import axios from 'axios'
export default {
  name: 'General',
  data: () => ({
    btc:[],
    bch:[],
    eth:[],
    ltc:[],
    xem:[],
    xrp:[],
    errors: [],
    first: false,
    second:false,
    byn0adr0:false,
    coin0:false,
    newTransaction: {
         byn: '0',
         coin: '0',
         adress: ''
     },
  }),
  created () {
    
    axios.get('https://min-api.cryptocompare.com/data/price?fsym=BTC&tsyms=USD')
      .then(response => {
        this.btc = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })

    axios.get('https://min-api.cryptocompare.com/data/price?fsym=BCH&tsyms=USD')
      .then(response => {
        this.bch = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })

    axios.get('https://min-api.cryptocompare.com/data/price?fsym=ETH&tsyms=USD')
      .then(response => {
        this.eth = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })

    axios.get('https://min-api.cryptocompare.com/data/price?fsym=LTC&tsyms=USD')
      .then(response => {
        this.ltc = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })

    axios.get('https://min-api.cryptocompare.com/data/price?fsym=XEM&tsyms=USD')
      .then(response => {
        this.xem = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })

    axios.get('https://min-api.cryptocompare.com/data/price?fsym=XRP&tsyms=USD')
      .then(response => {
        this.xrp = response.data
        console.log(response)
      })
      .catch(e => {
        this.errors.push(e)
      })


},

  methods:{
    exchange_btc(value){
        if(this.newTransaction.byn==0)
        return this.newTransaction.coin=0;
        else
        this.newTransaction.coin= (this.newTransaction.byn /value).toFixed(8);
    },
    exchange(value){
        if(this.newTransaction.byn==0)
        return this.newTransaction.coin=0;
        else
        this.newTransaction.coin= (this.newTransaction.byn /value).toFixed(4);
    },
    byn(value)
    {
        return value=((value+5*value/100)*2).toFixed(3);  
    },
    addTransaction: function () {
      if(this.newTransaction.byn==0||this.newTransaction.adress=='')
      {
        byn0adr0=true;
      }
      else if(this.newTransaction.coin==0)
      {
        coin0=true;
      }
      {
       transactionsRef.push(this.newTransaction);
       this.newTransaction.byn = '';
       this.newTransaction.coin = '';
      }
     },
     

  },
  firebase: {
   transactions: transactionsRef
 },
  
 
  
  }
</script>

<<style>

 .md-dialog-alert{
    width:300px;
    
  }
  .md-card {
    width: 330px;
    background-color:white; 
    display: inline-block;
    vertical-align: top;
  }


</style>


